// Footer
footer[role="contentinfo"] {
  @include position(fixed, null 0 0 0);
  background: $gray-4;
  font-family: $serif;
  font-size: 1.5em;
  z-index: 2;

  .foot-wrap {
    @include clearfix;
    padding: 10px 5%;
    max-width: $large-screen;
    margin: 0 auto;
  }

  a {
    @include link($iasr-white, $iasr-blue);
  }

  ul {
    list-style: none;

    li {
      display: inline;
      padding: 0 .25em;
    }
  }

  .footer-about {
    float: left;

    li:first-of-type {
      padding-left: 0;
    }
  }

  $networks: (
    twitter: $twitter-blue,
    linkedin: $linkedin-blue,
    github: $github-black
  );

  @each $network, $color in $networks {
    .social-#{$network} {
      @include link($iasr-white, $color);
    }
  }

  .footer-social {
    float: right;

    li:last-of-type {
      padding-right: 0;
    }
  }

  @media screen and (max-width: $small-screen) {
    @include position(relative, null 0 0 0);
    font-size: 1.5em;

    .footer-about,
    .footer-social {
      float: none;
      text-align: center;

      li {
        &:first-of-type,
        &:last-of-type {
          padding: 0 .25em;
        }
      }
    }

    .paplow-box {
      opacity: .1;
    }

    a {
      padding: .25em;
    }
  }
}
