// Header
.site-header {
  padding-top: .25em;
  font-size: 4em;
  text-align: center;

  .site-title {
    font-size: 1em;
    margin: 0;
    line-height: .85;

    a:focus {
      color: inherit;
    }
  }

  .site-nav {
    font-size: .4em;
    margin-bottom: .5em;

    a {
      @include link($iasr-blue, $gray-4);
      display: inline-block;
    }
  }

  @media screen and (max-width: $medium-screen) {
    font-size: 4em;
  }

  @media screen and (max-width: $small-screen) {
    font-size: 2.5em;
    padding: .5em 0;

    a {
      padding: .25em;
    }
  }
}
