@font-face {
	font-family: 'icomoon';
	src:url('../fonts/icomoon/fonts/icomoon.eot?-c9ftgl');
	src:url('../fonts/icomoon/fonts/icomoon.eot?#iefix-c9ftgl') format('embedded-opentype'),
		url('../fonts/icomoon/fonts/icomoon.ttf?-c9ftgl') format('truetype'),
		url('../fonts/icomoon/fonts/icomoon.woff?-c9ftgl') format('woff'),
		url('../fonts/icomoon/fonts/icomoon.svg?-c9ftgl#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
	font-family: 'icomoon';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Enable Ligatures ================ */
	letter-spacing: 0;
	-webkit-font-feature-settings: "liga";
	-moz-font-feature-settings: "liga=1";
	-moz-font-feature-settings: "liga";
	-ms-font-feature-settings: "liga" 1;
	-o-font-feature-settings: "liga";
	font-feature-settings: "liga";

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon-play-music:before {
	content: "\e600";
}
