// Boilerplate Overrides
#skip {
  @include hide-text;
  width: 1px;
  height: 1px;
  position: absolute;
}

// Bourbon Refills Overrides
$base-border-radius: 3px !default;
$base-line-height: 1.5em !default;
$base-spacing: 1.5em !default;
$base-accent-color: #6b83a6 !default;
$base-link-color: #6b83a6 !default;
$dark-gray: #333 !default;
$light-gray: #DDD !default;
$large-screen: 1280px;
$medium-screen: em(640) !default;
$base-font-color: $dark-gray !default;

h1, h2, h3, p {
  margin: 0;
}

hr {
  border-bottom: 1px solid $light-gray;
  border-left: none;
  border-right: none;
  border-top: none;
  margin: $base-spacing 0;
}

p {
  color: $base-font-color;
  line-height: $base-line-height;
}

a {
  color: $base-link-color;
  text-decoration: none;
}

$sans-serif: 'Muli', sans-serif;
$serif: 'Ovo', serif;

.type {
  border-bottom: 2px solid;
  display: inline-block;
  font-family: $sans-serif;
  font-size: .7em;
  font-weight: 800;
  margin-bottom: 2em;
  padding: .1em 0;
  text-align: left;
  text-transform: uppercase;
}

h1 {
  font-family: $serif;
  font-size: 1.9em;
  margin-bottom: 0.3em;
}

h2 {
  font-family: $serif;
  font-size: 1.3em;
  font-weight: 400;
  line-height: 1.25em;
  margin-bottom: .9em;

  @include media($medium-screen) {
    font-size: 1.5em;
  }
}

h3 {
  font-family: $serif;
  font-size: 1.4em;
  font-style: italic;
  font-weight: 400;
  line-height: 1.3em;
  margin-bottom: 0.4em;
}

// Prism overrides
pre[class*="language-"] {
  border: 0;
  font-size: .75em;
  margin-bottom: 1.5em;
}

p.date {
  color: transparentize($base-font-color, 0.6);
  font-family: $serif;
  font-style: italic;
  margin-bottom: .3em;
}

p {
  font-family: $sans-serif;
  letter-spacing: 1;
  line-height: 1.55em;
  margin-bottom: 1.5em;

  span {
    font-family: $serif;
    font-size: 1.2em;
    font-style: italic;
  }
}

a.read-more {
  display: inline-block;
  font-family: $sans-serif;
  font-size: 0.8em;
  font-weight: 700;
  margin-left: 0.2em;
  position: relative;
  text-transform: uppercase;

  span {
    font-family: $sans-serif;
    font-size: 1.5em;
    font-style: normal;
    position: absolute;
    right: -12px;
    top: -1px;
  }
}

hr {
  width: 3em;
}

p.author {
  font-family: $serif;
  font-size: 1.2em;
  font-style: italic;
}
